<template>

  <div class="container-md">
  <h2 class="text-center">Gallery</h2>
  
   <div v-for="post in posts" :key="post.id" class="col">
    
     <img class="img-fluid my-2" :src="post.data.course_image.url" alt="">
 </div>
 </div>

</template>

<script>
export default {
  name: "WorkshopList",
  data() {
    return {
      posts: [],
      isLoading: false,
    };
  },
  methods: {
    getPosts() {
      this.isLoading = true;
      //Query to get blog posts
      this.$prismic.client
        .query(this.$prismic.Predicates.at("document.type", "courses"), {
          orderings: "[document.first_publication_date desc]",
        })
        .then((response) => {
          this.posts = response.results;
          this.textLoaded = false;
          this.isLoading = false;
        });
    },

  },
  created() {
    this.getPosts();
  },
};
</script>