<template>
  <div class="container-md ">
    <div class="row d-flex justify-content-center">
      <div class="col-auto">
    <div
      class="card text-dark bg-light mb-3 text-center"
      style="max-width: 18rem"
    >

      <i style="font-size:100px;" class="bi bi-person-circle "></i>
      <div class="card-body">
        <h5 class="card-title">
          {{ fullName }} 
        </h5>
        <p class="card-text">{{ emailAddress }}</p>
        <p>Currently not enrolled onto any workshops</p>
      </div>
    </div>
    </div>
   
      <div class="col-10 mt-5">
 <h5 class="text-center">Students: </h5>

  <ErrorModal :error-text="error" :show="!!error" @click="handleError" />
  <LoadingSpinner :show="isLoading" />

 <table v-if="hasMembers" class="table table-striped">
  <thead>
    <tr>
      <th scope="col">#</th>
      <th scope="col">First</th>
      <th scope="col">Last</th>
      <th scope="col">Email</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(member, index) in listOfMembers" :key="index">
      <th scope="row">{{index +1}}</th>
      <td>{{member.firstName}}</td>
      <td>{{member.lastName}}</td>
       <td>{{member.email}}</td>
   
    </tr>

  </tbody>
</table>

  <p v-else>No members.</p>
  </div></div>


  </div>
</template>
<script>
export default {
  data() {
    return {
  isLoading: false,
      error: null,
    };
  },
  created() {
     this.loadMembers();
    this.loadCurrentUser();
  },
  methods: {
       async loadMembers() {
      this.isLoading = true;
      try {
        await this.$store.dispatch("members/loadMembers");
      } catch (error) {
        this.error = error.message || "Something went wrong!";
      }
      this.isLoading = false;
    },
    handleError() {
      this.error = false;
    },
    async loadCurrentUser() {
      try {
        await this.$store.dispatch("members/fetchThisUser");
      } catch (error) {
        this.error = error.message || "Something went wrong!";
      }
    },
  },
  computed: {
    fullName() {
      return this.$store.getters["members/currentUser"]?.firstName + ' ' + this.$store.getters["members/currentUser"]?.lastName
    },
     emailAddress() {
      return this.$store.getters["members/currentUser"]?.email;
    },
       listOfMembers() {
      return this.$store.getters["members/members"];
    },
    hasMembers() {
      return !this.isLoading && this.$store.getters["members/hasMembers"];
    },
  },
};
</script>